.footer {
  padding-bottom: 10px;
  text-align: center;
  &__contact {
    padding: 20px 0;
    display: flex;
    justify-content: center;
    li {
      margin: 15px 0;
      display: flex;
      align-items: center;
      p {
        margin-left: 10px;
        font-size: 18px;
      }
    }
  }
  &__logo {
    font-family: "Permanent Marker", arial, sans-serif;
    font-size: 24px;
  }
}
