.category {
  &:hover &__title {
    letter-spacing: 5px;
    transform: scale(1.4);
  }
  &:hover &__image div {
    filter: blur(5px);
  }
  &__image {
    width: 50%;
    height: 50vw;
    max-height: 650px;
    max-width: 650px;
    overflow: hidden;
  }
  &__title {
    text-align: center;
    font-size: 16px;
    font-weight: 300;
    letter-spacing: 2px;
    width: 50%;
  }
}
@media (min-width: 768px) {
  .category {
    &__title {
      font-size: 24px;
    }
  }
}
