.header {
  display: flex;
  align-items: center;
  height: 60px;
  padding: 0 15px;
  max-width: 1300px;
  margin: 0 auto;
  &__bars {
    border: none;
    background: none;
    cursor: pointer;
  }
  &__logo {
    margin: 0 auto 0 15px;
    p {
      font-family: "Permanent Marker", arial, sans-serif;
      font-size: 24px;
    }
  }
  &__menu {
    display: none;
    flex: 0.5;
  }
  &__list {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  &__item {
    flex-basis: 25%;
    text-align: center;
    border-bottom: 2px solid rgba(0, 0, 0, 0);
    &:hover {
      border-bottom: 2px solid rgba(0, 0, 0, 1);
    }
    a {
      font-size: 18px;
      padding: 5px 0;
      display: block;
      text-transform: uppercase;
    }
  }
  &__cart {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    p {
      margin-left: 10px;
    }
  }
  @media (min-width: 1024px) {
    &__bars {
      display: none;
    }
    &__menu {
      display: flex;
    }
  }
}
@media (min-width: 1320px) {
  .header {
    padding: 0 0;
  }
}
