* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  letter-spacing: 1px;
  transition: 0.2s;
}

html {
  font-family: "Source Sans Pro", arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.container {
  max-width: 1300px;
  margin: 0 auto;
}

a {
  color: black;
  text-decoration: none;
}

li {
  list-style-type: none;
}

